import React, {Component} from 'react';
import {connect} from 'react-redux'
import {List, ListItem, Wrapper, StyledLink} from './styled';
import {showModal} from "../../common";
import {noop} from "../../../constants";

@connect(null, {showModal})
export class BidList extends Component {
	
	handleClickU = (e) => {
		e.preventDefault();
		const {showModal = noop} = this.props;
		
		showModal('bid_form_u');
	};
	
	handleClickF = (e) => {
		e.preventDefault();
		const {showModal = noop} = this.props;
		
		showModal('bid_form_f');
	};
	
  render(){
		
    return (
	    <List>
		    <ListItem>
			    <Wrapper>
				    <h3>   </h3>
				    <StyledLink href="#req_indiv" title="      " onClick={this.handleClickF}> </StyledLink>
			    </Wrapper>
			    <img src="/src/img/worker@1x.png" srcSet="/src/img/worker@2x.png 2x" width="103" height="120"
			         alt=" " title=" "/>
		    </ListItem>
		    <ListItem>
			    <Wrapper>
				    <h3> </h3>
				    <StyledLink href="#req_entity" title="    " onClick={this.handleClickU}> </StyledLink>
			    </Wrapper>
			    <img src="/src/img/manager@1x.png" srcSet="/src/img/manager@2x.png 2x" width="83" height="120"
			         alt=" "
			         title=" "/>
		    </ListItem>
	    </List>
    )
  }
};
